.my_container{
    margin-top: 180px;
    margin-left: 10%;
    width: 26.5%;

}
.my_container img{
width: 420px;
height: 450px;
filter: drop-shadow(2px 5px 10px);
cursor: pointer;
}
.my_container img:hover{
transform: scale(1.5);
transition: ease-in-out 0.1s;
}
.my_container1{
width: 10%;
margin-top: -31.5%;
filter: drop-shadow(1px 4px 6px);
}
.my_container1 img{
    width: 90px;
    height: 100px;
    border-style: groove;
    border-radius: 10%;
    box-shadow: 2px 5px 10px;
}
.my_container1 img:hover{
    transform: scale(0.8);
    transition: ease-in-out 0.1s;
}
